import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-struct',
  templateUrl: './struct.component.html',
  styleUrls: ['./struct.component.css']
})
export class StructComponent implements OnInit {
  
   perSons = [
    { id: 1, name: 'A', age: '34'  },
    { id: 2, name: 'B', age: '23' },
    { id: 3, name: 'C', age: '45'  },
    { id: 4, name: 'D', age: '67' },
    { id: 5, name: 'E', age: '23' },
    { id: 6, name: 'F', age: '56'}
  ];
   
  value = 0;

  constructor() { }

  ngOnInit() {
  }

  changeValue(event){
    if(event){
      this.value = this.value+1;
    }else{
      this.value = this.value-1;
    }
  }



}
